import { faRoad } from '@fortawesome/free-solid-svg-icons';
import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlay, faPause, faAngleLeft, faAngleRight } from '@fortawesome/free-solid-svg-icons';
import userEvent from '@testing-library/user-event';

function Player({ setCurrentSong, songs, songInfo, setsongInfo, currentSong, isPlaying, setIsPlaying, audioRef }) {

    function playSongHandler() {
        //we have set isPlaying to {false}
        if (isPlaying == false) {
            audioRef.current.play();
            setIsPlaying(true)
            console.log("play");

        } else {
            audioRef.current.pause();
            setIsPlaying(false);
            console.log("pause");
        }

    }
    //state



    function dragHandler(e) {
        audioRef.current.currentTime = e.target.value;
        setsongInfo({ ...songInfo, currentTime: e.target.value });
        console.log(e.target.value);
    }

    function skipTrackHandler(direction) {
        let currentIndex = songs.findIndex((song) => song.id === currentSong.id);
        if (direction === 'skip-forward') {
            if (currentIndex + 1 < songs.length) {
                setCurrentSong(songs[currentIndex + 1]);
                console.log(currentIndex);
            }
            else {
                setCurrentSong(songs[songs.length - 3]);
            }
        }
        else if (direction === 'skip-back') {
            if (currentIndex === 0) {
                setCurrentSong(songs[currentIndex])
            } else {
                setCurrentSong(songs[currentIndex - 1])
            }

        }

    }


    function playbackTime(time) {
        if (time != null) {
            return Math.floor(time / 60) + ":" + ("0" + Math.floor(time % 60)).slice(-2)
        }
        else {
            return "Loading";
        }
    }

    return (
        <div className="player-container">
            <div className="time-controller">
                {/* <p> {Math.floor(songInfo.currenTime / 60) + ":" + ("0" + Math.floor(songInfo.currenTime % 60)).slice(-2)}</p> */}
                <p> {playbackTime(songInfo.currenTime)}</p>
                <input type="range" min={0}
                    max={songInfo.duration || 0}
                    onChange={dragHandler}
                    value={songInfo.currenTime} />

                {/* <p>{Math.floor(songInfo.duration / 60) + ":" + ("0" + Math.floor(songInfo.duration % 60)).slice(-2) ?? "loading"}</p> */}
                <p>{playbackTime(songInfo.duration)}</p>
            </div>
            <div className="play-control">
                <FontAwesomeIcon onClick={() => skipTrackHandler('skip-back')} className='skip-back' size='2x' icon={faAngleLeft} />
                <FontAwesomeIcon onClick={playSongHandler} className='play' size='2x' icon={isPlaying === false ? faPlay : faPause} />
                <FontAwesomeIcon onClick={() => skipTrackHandler('skip-forward')} className='skip-forward' size='2x' icon={faAngleRight} />

            </div>
        </div>

    );
}

export default Player;