import React from 'react';

function Song({ currentSong }) {
    return (
        <div className="song-container">
            <img src={currentSong.cover}></img>
            <div className="text-area">
                <br />
                <h1>{currentSong.name}</h1>
                <br />
                <h2>{currentSong.artist}</h2>
            </div>
        </div>
    );
}

export default Song;