import React, { useState, useRef } from 'react';
import Player from './components/Players';
import Song from './components/Song';
import "./styles/app.scss";
import chillHop from './util';
import Library from './components/Library';
import Nav from './components/Nav';

function App() {
  //State
  const audioRef = useRef(null);
  const [songs, setSongs] = useState(chillHop());
  const [currentSong, setCurrentSong] = useState(songs[0]);
  const [isPlaying, setIsPlaying] = useState(false);
  const [songInfo, setsongInfo] = useState({
    currenTime: null,
    duration: null,
  });
  const [libraryStatus, setLibraryStatus] = useState(false);


  function timeUpdateHandler(e) {
    const current = e.target.currentTime;
    const duration = e.target.duration;
    setsongInfo({ ...songInfo, currenTime: current, duration });
    //  console.log(songInfo.duration);
    // console.log(duration);
  }

  return (
    <div className="App">
      <Nav libraryStatus={libraryStatus} setLibraryStatus={setLibraryStatus} />
      {/* When we aff "currentSong= {currentSongs}" this mean we are transfering all the datas to the Song and the player's component */}
      <Song currentSong={currentSong} />
      <Player setCurrentSong={setCurrentSong} songs={songs} songInfo={songInfo} setsongInfo={setsongInfo} audioRef={audioRef} setIsPlaying={setIsPlaying} isPlaying={isPlaying} currentSong={currentSong} />
      <Library libraryStatus={libraryStatus} setSongs={setSongs} isPlaying={isPlaying} audioRef={audioRef} songs={songs} setCurrentSong={setCurrentSong} />
      <audio onLoadedMetadata={timeUpdateHandler} onTimeUpdate={timeUpdateHandler} ref={audioRef} src={currentSong.audio}></audio>
    </div>
  );
}

export default App;
